/**
 * 
 * 
 */
import Vue from 'vue'
import { throttle } from 'lodash'

export const IScrollOption = {
  distance: Number, // loadData
  loadData: () => {} // 
}
/**
 *  ; ,
 * @param dom
 * @param option
 */
export function handleScroll(dom, option) {
  //
  const scrollBottom = dom.scrollTop + dom.clientHeight

  // console.log(scrollBottom, dom.scrollHeight)
  if (dom.scrollHeight - scrollBottom <= option.distance) {
    option.changeCurPage()
    // 
    option.loadData()
  }
}

export const ILazyProps = {
  loadData: () => {}, // 
  changeCurPage: () => {}, // 
  distance: Number, // 
  scrollBody: String, //  , 
  callback: (fn) => fn() // ,
}

// 
Vue.directive('lazy-load', {
  /**
   * el - DOM
   * binding - 
   *    name - 
   *    value  - 
   *    oldValue - 
   *    expression - 
   *    arg - 
   *    modifiers - 
   * vnode
   * oldVnode
   */
  inserted: (el, binding) => {
    // console.log('inserted', el, binding)
    // scroll ,
    // ,
    //  , 
    const { loadData, distance, scrollBody, changeCurPage, callback } = binding.value
    let scrollContainer = el
    if (scrollBody) {
      scrollContainer = el.querySelector(scrollBody) || el
    }

    // 
    const scroll = throttle(
      handleScroll.bind(null, scrollContainer, { distance, loadData, changeCurPage }),
      500
    )
    scrollContainer.addEventListener('scroll', scroll)

    //  
    callback(() => {
      // console.log('callback')
      scrollContainer.removeEventListener('scroll', scroll)
    })
  }
})

//  
Vue.directive('no-more', {
  inserted(el, binding) {
    el.addEventListener('click', (e) => {
      el.classList.add('is-disabled')
      el.disabled = true
      setTimeout(() => {
        el.disabled = false
        el.classList.remove('is-disabled')
      }, 3000)
    })
  }
})



