import {
  post
} from '../axios'

export default {
  //  params: { 'id': id },data: { model: model }
  findAllGeneratroSetOrbitRecordV2: function (data, params) {
    return post(data, `/GeneratorSet/FindAllGeneratroSetOrbitRecordV2?generatorSetId=${params.generatorSetId}`)
  },
  //  params: { 'id': id },data: { model: model }
  gensetFenceSetting: function (data) {
    // return post(data, '/GeneratorSet/GensetFenceSetting')
    return new Promise(function (resolve, reject) {
      let flag = 1
      setTimeout(() => {
        if (flag === 1) {
          resolve({
            code: 1
          })
        } else reject(new Error('error'))
      }, 3000)
    })
  }
}



