  // ID
  GetGensetFenceSetting: function (params) {
    return (
      get(params, '/GeneratorSet/GetGensetFenceSetting')
        .then((value) => {
          //  GPS->BD-09 GPSBD09
          const data = !!value && !!value.Data && value.Data
          if (data) {
            const newCoordinate = coordinateHandler.gps_bd_encrypt(data.Latitude, data.Longitude)
            data.Longitude = newCoordinate.Longitude
            data.Latitude = newCoordinate.Latitude
            if (!!data.Genset) {
              const newCoordinate = coordinateHandler.gps_bd_encrypt(
                data.Genset.Latitude,
                data.Genset.Longitude
              )
              data.Genset.Longitude = newCoordinate.Longitude
              data.Genset.Latitude = newCoordinate.Latitude
            }
          }
          return value
        })
        // .catch((rej) => rej)
        .catch((reason) => {
          // terminate Promise
          throw reason
        })
    )
  },

